@import '../base';
@import '../mixins/icons';

@import './adder';

$base-font-size: 14px;

//HIGHLIGHTS////////////////////////////////
[clip-highlights-always-on] {
  .webclip-hl {
    background-color: $clip-highlight-color;
  }
 
  .webclip-exclude {
    background-color: #fff !important;
  }
 
  [data-clipped='true'] {
    background: $clip-highlight-color;
  }
  
  [_numici='exclude'],[data-clipped='false'] {
    background:  #fff;
  }
}

[annotator-highlights-always-on] {
  .annotator-hl {
    background-color: $highlight-color;
    img {
    	border: 3px solid rgba(255, 255, 60, 1);
    }

    // cursor pointer gives mobile devices click
    // event support. Using the pointer:coarse gives
    // us good targeting to mobile devices that this effects
    @media (pointer:coarse) {
      cursor: pointer;
    }
  }
  .annotator-hl .annotator-hl {
    background-color: $highlight-color-second;
  }

  .annotator-hl .annotator-hl .annotator-hl {
    background-color: $highlight-color-third;
  }
}

.numici-screenshot.annotator-hl-focused {
	//background-color: #ff000066;
}

.annotator-hl.annotator-hl-focused {
  background-color: $highlight-color-focus !important;
  img {
    border: 3px solid rgba(255, 165, 0, 1) !important;
  }
  .annotator-hl {
    background-color: transparent !important;
  }
}

.annotator-hl.image-h1 {
	display: contents;
	position: relative;
	width: inherit;
	height: inherit;
    max-width: 100%;	
}

.annotator-hl.image-h1:before {
	content: "\e903";
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1000;
    color: rgba(255, 255, 60, 0.8);
    font-size: 20px;
    background: rgba(255, 165, 0, 0.9);
    font-family: h !important;
    padding: 5px;
    box-shadow: 3px 3px 4px 0px #000;
}

.annotator-hl.annotator-hl-focused.image-h1:before {
	color: rgba(255, 255, 60, 1);
	background: rgba(255, 165, 0, 1);
} 

// Sidebar
.annotator-frame {
  // a CSS reset which attempts to prevent the host page's styles
  // from affecting the styles of the sidebar.
  //
  // FIXME - This component and other annotator components which currently
  // live on the page could be isolated from the host page's CSS/JS via
  // transparent same-origin iframes
  @import '../reset';
  @include nested-reset;
  @include reset-box-model;

  // frame styles
  @import './bucket-bar';
  user-select: none;
  @include grey-background;
  direction: ltr;
  background: none;
  font-size: $base-font-size;
  line-height: $base-line-height;
  height: 100% !important;
  position: fixed;
  top: 0;
  left: 100%;
  z-index: 2147483646;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);

  &.annotator-collapsed {
    margin-left: 0;
    .annotator-bucket-bar {
    	//width: 10px !important;
       // left: -10px !important;
       // background: #069 !important;
       pointer-events: none;
    }
   .annotator-toolbar{
   	 height: 100%;
   }
  }
  .numici-icon-right {
  	background-image: url(../images/icons/Numici-collapse.png) !important;
  }
 
  .numici-icon-left {
  	background-image: url(../images/icons/Numici-exp.png)  !important;
  }
 
  * {
    background: none;
    font-size: 100%;
    text-indent: 0;
    height: initial;
    width: initial;
  }

  .h-sidebar-iframe {
    border: none;
    height: 100%;
    width: 100%;
    z-index: 3;
    position: relative;
  }

  @include icons {
    font-family: 'h' !important;
  }

  // the vertical toolbar at the left-edge of the sidebar
  // which provides controls for toggling the sidebar,
  // toggling highlights etc.

  // note: These components CSS classes are nested inside
  // .annotator-frame to avoid being overridden by the
  // CSS reset styles applied to .annotator-frame, which would otherwise
  // have higher specificity
  
   //left: -($bucket-bar-width + 18px - 7px);
  .annotator-toolbar {
    position: absolute;
    left: -35px;
    width: 35px;
    z-index: 2;
    text-align: right;

    ul {
      //height: 100%;
    }

    ul, li {
      box-sizing: border-box;
      list-style: none;
      @include reset-box-model;
    }
   .rotate90 {
   	  transform: rotate(90deg);
   }
  }

  .annotator-frame-button {
    transition: background-color .25s .25s;
    @include smallshadow;
    background: $white;
    border: solid 1px $gray-lighter;
    border-radius: 4px;
    color: $gray-light;
    text-decoration: none;
    height: $btn-25 !important;
    line-height: $btn-25 !important;
    width: $btn-25;
    min-width: $btn-25 !important;
    max-width: $btn-25 !important;
    min-height: $btn-25 !important;
    max-height: $btn-25 !important;
    
    padding: 0;
    font-size: $btn-font-size;
    margin-bottom: 5px;

    &:active {
      transition: background-color .25s;
      background-color: $gray-light;
    }

    &:focus, &:hover {
      outline: 0;
      //color: $text-color;
    }
   
    &.h-icon-note:before {
       color: #fff;
    }
   
    &.h-icon-annotate:before {
   	   color: #fff;
    }
   
    &.h-icon-annotation-share:before{
       color: #fff;
    }
   
    &.h-icon-insert-photo {
   	  font-size: 16px;
   	  &.img-on {
		border: 2px solid $numici-blue;
		background: #ffff33;
	  }
	  &.img-on:before {
		color: $numici-blue !important;
	  }
   }
  
  }
 
  .annotator-frame-button {
	  background: $numici-blue;
	  color: #fff;
	  text-align: center !important;
  }
  

  .annotator-frame-button--sidebar_toggle {
    box-shadow: none;
    //border-radius: 0px;
    //border-style: none none solid solid;
    //padding-right: 6px;
    //width: 30px;
    //min-width: 30px !important;
    //max-width: 30px !important;
    //margin-bottom: 10px;
    margin: 5px 0px;
    // the height of the sidebar toggle is set
    // to match the height of the top bar
    //height: 30px;
    //min-height: 30px !important;
    //max-height: 30px !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-size: contain !important;
    background-color: $numici-blue !important;
  }

  .annotator-frame-button--sidebar_close {
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.5);
    border-radius: 0px;
    border-style: solid none solid solid;
    width: 27px;
    margin-top: 140px;
    margin-left: 6px;
    height: 27px;
  }
}

.annotator-frame--drop-shadow-enabled {
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
}

.annotator-placeholder {
  opacity: 0;
  position: absolute;
  top: 50%;
  z-index: -1;
}

// this disables the width transition for the sidebar when
// it is manually resized by dragging
.annotator-no-transition {
  transition: none !important;
}

/*
  Mobile layout
  240-479 px
  Zoomed out below 320 px
*/

@media screen and (min-width: 15em) {
  .annotator-frame {
    width: 90%;
    margin-left: -90%;
  }
}

/*
  Wide mobile layout
  480-767 px
  Zoomed in above 480 px
*/

@media screen and (min-width: 30em) {
  .annotator-frame {
    width: 70%;
    margin-left: -70%;
  }
}

/*
  Tablet layout
  600-911 px
  Zoomed in above 600 px
*/

@media screen and (min-width: 37.5em) {
  .annotator-frame {
    transition: margin-left .15s cubic-bezier(.55, 0, .2, .8);
    width: 428px;
    margin-left: -428px;
  }
}

/*
  Widescreen layout
  912-1887 px
  Zoomed in above 912 px
*/

@media screen and (min-width: 57em) {
