.clipper-frame {
	position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #00000070;
    z-index: 2147483647;
    
    iframe {
    	position: absolute;
	    width: 100%;
	    height: 100%;
	    border: none;
    }
   
   &.clipper-collapsed {
   		display: none;
   }
}

.clipper-toolbar {
	position: absolute;
    right: 40px;
    top: 80px;
    z-index: 1;
    
    button {
    	background: none;
	    border: none;
	    font-size: 20px;
	    padding: 0;
   }
}


button[disabled] {
	cursor: not-allowed;
}

button[disabled]:before {
	color: #777;
}
